
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}




















































































































































































































































































*{
   margin:0;
   padding:0;
}
h1{
   height:45px;
   width: 100%;
   background-color: #FFF;
   text-align: center;
   padding-top: 25px;
   font-size: 20px;
   color: #626262;

   border-radius: 0 0 30px 30px;

   margin-bottom: 30px;

   position: fixed;
   top:0px;

   -webkit-box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
   box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
}
#user_image{
   width:135px;
   height: 135px;
   -o-object-fit: cover;
      object-fit: cover;
   border-radius: 50%;

   border:solid 7px #FFF;
   -webkit-box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
   box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
}
#user_name{
   margin-top:15px;
   font-size: 20px;
}

/*項目*/
ul{
   margin-top:30px;
   margin-bottom: 60px;
}
li{
   margin: 10px 0;
   -webkit-box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.15);
   box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.15);
}
li button{
   width: 100%;
   font-size: 17px;
   border:none;
   height: 50px;
   background-color: #FFF;
   color: #626262;
   text-align: center;

   -webkit-transition: all 0.2s ease-out;

   transition: all 0.2s ease-out;
}
li button:active{
   color: #FFF;
   background-color: #AAA;
}

/*ログアウト*/
#log_out button{
   font-size: 17px;
   color:#C40303;

   width: 100%;
   margin-bottom: 20px;
   height: 50px;
   background-color: #FFF;
   text-align: center;

   border:none;

   -webkit-box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
   box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);

   -webkit-transition: all 0.2s ease-out;

   transition: all 0.2s ease-out;
}
#log_out button:active{
   background-color: #AAA;
}
.spacing{
   height: 100px;
}






































































































































*{
    margin:0;
    padding:0;
}
.title{
    height:45px;
    width: 100%;
    background-color: #FFF;
    text-align: center;
    padding-top: 25px;
    font-size: 10px;
    color: #626262;
    border-radius: 0 0 30px 30px;
    margin-bottom: 30px;
    position: fixed;
    top:0px;

    z-index: 100;

    -webkit-box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
}
.title button,.title img{
    width:15px;
    border:none;
}
.title h1{
  margin-right: 65px;
  display:block;
  background-color: transparent;
}
.title button{
  z-index: 1000;
  margin-left: 30px;
  width:33px;
  height: 33px;
  float: left;
  background-color: #FFF;
}

 /* シェアボタン */
.share {
  margin-top: 40px;
  clear: both;
}
.share ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.share li a {
  display: block;
  text-align: center;
  padding: 10px 5px;
  color: #ffffff;
  font-size: 12px;
  text-decoration: none;
}
.share li a:hover {
  opacity: 0.8;
}
.share ul:after {
  content: "";
  display: block;
  clear: both;
}
.share li {
  margin:10px auto;
  width: 100%;
  -webkit-box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 5px 50px 0px rgba(0,0,0,0.2);
}
.share li p {
  display: inline-block;
  padding-top: 10px;
}
.bunsho{
  text-align: center;
  margin: 30px auto;
}
.share-tw {
  height: 40px;
  background-color: #55acee;
}
.share-fb {
  height: 40px;
  background-color: #3b5998;
}
.share-gp {
  height: 40px;
  background-color: #00B900;
}
.share-hb {
  height: 40px;
  background-color: #2c6ebd;
}
.hatenaB:before {
  content: 'B!';
  font-style: normal;
}
@media (max-width: 599px) {
.share span {
    display: inline-block;
    text-indent: -9999px;
}
.share li a {
    padding: 8px 10px;
}
}














































